<script>
// API
import { outExportExcel } from '@/api/mainApi'
// API
import {
  planWorkorderInfoQueryListByPage,
  planWorkorderInfoQueryList,
  planWorkorderInfoInsertList,
  planWorkorderInfoInsert,
  planWorkorderInfoUpdateList,
  planWorkorderInfoUpdate,
  planWorkorderInfoDelete
} from '@/api/em-ep/planWorkorderInfoApi.js'

export default {
  data () {
    return {
      activeName: 'first',
      //  
      dialogAddVisible: false,
      //  
      dialogEditVisible: false,
      //  
      dialogDelVisible: false,
      // tab 
      activeIndex2: '1',
      // 
      total: 0,
      size: 5,
      page: 1,
      // 
      queryParam: {},
      // 
      dataList: [],
      // 
      insertParam: {},
      // 
      editParam: {},
      // 
      delParam: {},
      // 
      tableHeader: [
        '',
        '',
        '',
        '',
        '',
        '',
        '',
        '',
        '',
        '',
        '',
        '',
        '',
        '',
        '',
        ''
      ],
      // key
      tableKey: [
        'orderId',
        'orderType',
        'siteId',
        'instrumentId',
        'responsiblerPerson',
        'collaborator',
        'orderDesc',
        'orderImg',
        'priority',
        'orderStatus',
        'planCompletionDay',
        'receiveTime',
        'startTime',
        'completionTime',
        'createTime',
        'creator'
      ],
      // 
      tableData: [],
      //  '*'
      rules: {
        orderId: [{ required: true, message: '', trigger: 'blur' }]
      }

    }
  },
  watch: {
    // 2.xbug 1.x 5
    // listpage3
    total () {
      if (this.total == (this.page - 1) * this.size && this.total != 0) {
        this.page -= 1
        this.getDataList()
      }
    }
  },
  methods: {
    handleClick (tab, event) {
      console.log(tab, event)
    },
    handleClose (done) {
      done()
    },

    handleSizeChange (val) {
      this.size = val
      this.getDataList()
    },

    handleCurrentChange (val) {
      this.page = val
      this.getDataList()
    },

    // 
    async getDataList () {
      this.queryParam.pageNum = this.page
      this.queryParam.pageSize = this.size
      planWorkorderInfoQueryListByPage(this.queryParam).then((response) => {
        const resultCode = response.resultCode
        if (resultCode === '2000') {
          // 
          this.total = response.resultEntity.total
          this.dataList = response.resultEntity.list
        } else {
          // 
          alert(response.resultMsg)
        }
      })
    },
    // 
    resetForm () {
      this.queryParam = {}
      this.queryParam.pageNum = this.page
      this.queryParam.pageSize = this.size
    },

    //   
    addData () {
      this.dialogAddVisible = true
      this.insertParam = {}
    },
    // 
    async insertData (insertParam) {
      planWorkorderInfoInsert(insertParam).then((response) => {
        const resultCode = response.resultCode
        if (resultCode === '2000') {
          // 
          alert(response.resultMsg)
          // 
          this.dialogAddVisible = false
          // 
          this.getDataList()
        } else {
          // 
          alert(response.resultMsg)
        }
      })
    },

    //  
    editData (row) {
      // 
      this.editParam = JSON.parse(JSON.stringify(row))
      this.dialogEditVisible = true
    },
    // 
    async updateData (editParam) {
      planWorkorderInfoUpdate(editParam).then((response) => {
        const resultCode = response.resultCode
        if (resultCode === '2000') {
          // 
          alert(response.resultMsg)
          // 
          this.dialogEditVisible = false
          // 
          this.getDataList()
        } else {
          // 
          alert(response.resultMsg)
        }
      })
    },

    // 
    delData (row) {
      this.delParam = row
      this.dialogDelVisible = true
    },
    // 
    async handleDel () {
      const params = {
        orderId: this.delParam.orderId
      }
      planWorkorderInfoDelete(params).then((response) => {
        const resultCode = response.resultCode
        if (resultCode === '2000') {
          // 
          alert(response.resultMsg)
          this.dialogDelVisible = false
          // 
          this.getDataList()
        } else {
          // 
          alert(response.resultMsg)
        }
      })
    },
    // execl
    async exportExecl () {
      // 
      const params = {}
      planWorkorderInfoQueryList(params).then((response) => {
        // 
        this.tableData = response.resultEntity
        console.log(this.tableData)
        outExportExcel(this.tableHeader, this.tableKey, this.tableData, '')
      })
    }

  },
  mounted () {
    this.getDataList()
  }
}
</script>

