import myrequest from '@/utils/axios'

/** : */
export function monitorAlarmDataQueryListByPage(params) {
	return myrequest({
		url: '/em-ep/MonitorAlarmDataController/queryListByPage',
		method: 'post',
		data: params
	})
}

